import { useAuth0 } from "@auth0/auth0-react";
import { useCallback, useEffect, useState } from "react";
import { useMediaQuery } from "react-responsive";

export function useToken() {
  const { getAccessTokenSilently } = useAuth0();

  const fetchToken = useCallback(
    () =>
      getAccessTokenSilently({
        authorizationParams: {
          audience: "https://dev-nej4i3mnn265vjef.eu.auth0.com/api/v2/",
          scopes: ["openid", "profile", "email"],
        },
      }),
    [getAccessTokenSilently]
  );

  return useFetch(fetchToken);
}

export interface MobileQuery {
  isMobile?: boolean;
  isLaptop?: boolean;
}

export function useMobileQuery(): MobileQuery {
  const isPortrait = useMediaQuery({ query: "(orientation: portrait)" });
  return { isLaptop: !isPortrait, isMobile: isPortrait };
}

export function useToggle(init: boolean = false): [boolean, () => void] {
  const [value, setValue] = useState(init);
  const toggle = useCallback(() => setValue((v) => !v), [setValue]);
  return [value, toggle];
}

export function useBoolean(
  init: boolean = false
): [boolean, () => void, () => void] {
  const [value, setValue] = useState(init);
  const setTrue = useCallback(() => setValue(true), [setValue]);
  const setFalse = useCallback(() => setValue(false), [setValue]);
  return [value, setTrue, setFalse];
}

export interface FetchResult<T> {
  data: T | undefined;
  loading: boolean;
  error: Error | undefined;
  doRefetch: () => void;
}

export function useFetch<T>(source: () => Promise<T>): FetchResult<T> {
  const [data, setData] = useState<T>();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<Error>();
  const [refetch, doRefetch] = useToggle();

  useEffect(() => {
    source()
      .then((value) => {
        setData(value);
        setLoading(false);
      })
      .catch((e) => {
        setError(e);
        setLoading(false);
        console.log({ e });
      });
  }, [source, setData, setLoading, setError, refetch]);

  return { data, loading, error, doRefetch };
}
