import React, { PropsWithChildren } from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import reportWebVitals from "./reportWebVitals";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";
import { Auth0Provider } from "@auth0/auth0-react";
import AppRouter from "./routes";
import { ThemeProvider } from "@mui/material";
import defaultTheme from "./theme";

const MaterialTheme = (props: PropsWithChildren) => (
  <ThemeProvider theme={defaultTheme}>{props.children}</ThemeProvider>
);

const queryClient = new QueryClient();

const ApiClient = (props: PropsWithChildren) => (
  <QueryClientProvider client={queryClient}>
    {props.children}
  </QueryClientProvider>
);

const Authentication = (props: PropsWithChildren) => (
  <Auth0Provider
    domain={"dev-nej4i3mnn265vjef.eu.auth0.com"}
    clientId={"QVnd6bhoPFzoa8xleNQ3vsfTjQUXPGcb"}
    authorizationParams={{
      redirect_uri: window.location.origin,
      audience: "https://dev-nej4i3mnn265vjef.eu.auth0.com/api/v2/",
      scopes: ["openid", "profile", "email"],
    }}
  >
    {props.children}
  </Auth0Provider>
);

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
root.render(
  <React.StrictMode>
    <ApiClient>
      <Authentication>
        <MaterialTheme>
          <AppRouter />
        </MaterialTheme>
      </Authentication>
    </ApiClient>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
