import React, { useContext } from "react";
import { useAccountsServiceGetCurrentAccount } from "../services/servicesComponents";
import { DashboardContext } from "../layout/DashboardLayout";

export default function Profile() {
  const { headers } = useContext(DashboardContext);
  const { data } = useAccountsServiceGetCurrentAccount({ headers });

  return <>{JSON.stringify(data)}</>;
}
