import { useAuth0 } from "@auth0/auth0-react";
import { Link, useNavigate } from "react-router-dom";
import { useCallback } from "react";
import {
  AppBar,
  Avatar,
  Box,
  Button,
  Drawer,
  Grid,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Toolbar,
  Typography,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import {
  AccountCircle,
  Assignment,
  Home,
  Logout,
  Person,
  Person2,
} from "@mui/icons-material";
import { useBoolean } from "../hooks";

export function Navigation() {
  const [isShown, show, hide] = useBoolean();

  const navigate = useNavigate();
  const handleNavigate = useCallback(
    (to: string) => () => navigate(to),
    [navigate]
  );

  const { isAuthenticated, loginWithRedirect, logout } = useAuth0();
  const handleLogin = useCallback(
    () => loginWithRedirect(),
    [loginWithRedirect]
  );
  const handleLogout = useCallback(
    () =>
      logout({
        logoutParams: {
          returnTo: window.location.origin,
        },
      }),
    [logout]
  );

  const LoginButton = useCallback(
    () =>
      isAuthenticated ? (
        <>
          <Button color="inherit" endIcon={<Logout />} onClick={handleLogout}>
            Salir
          </Button>
        </>
      ) : (
        <>
          <Button
            color="inherit"
            endIcon={<AccountCircle />}
            onClick={handleLogin}
          >
            Autenticarse
          </Button>
        </>
      ),
    [isAuthenticated, handleLogout, handleLogin]
  );

  return (
    <>
      <AppBar position="sticky">
        <Toolbar variant="dense">
          <IconButton
            size="small"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
            onClick={show}
          >
            <MenuIcon />
          </IconButton>
          <Link to="/">
            <Grid container alignItems="center">
              <Grid item>
                <Avatar src="/playstore.png" />
              </Grid>
              <Grid item>
                <Typography variant="h6" fontWeight="bold">
                  Audite sus cuentas
                </Typography>
              </Grid>
            </Grid>
          </Link>
          <Box flexGrow={1} />
          <LoginButton />
        </Toolbar>
      </AppBar>
      <Drawer open={isShown} anchor="left" onClose={hide}>
        <Box width={250} role="presentation" onClick={hide} onKeyDown={hide}>
          <List>
            <ListItem disablePadding>
              <ListItemButton onClick={handleNavigate("/")}>
                <ListItemIcon>
                  <Home />
                </ListItemIcon>
                <ListItemText primary="Inicio" />
              </ListItemButton>
            </ListItem>
            {isAuthenticated ? (
              <>
                <ListItem disablePadding>
                  <ListItemButton onClick={handleNavigate("/dashboard")}>
                    <ListItemIcon>
                      <Assignment />
                    </ListItemIcon>
                    <ListItemText primary="Casos" />
                  </ListItemButton>
                </ListItem>
                <ListItem disablePadding>
                  <ListItemButton
                    onClick={handleNavigate("/dashboard/profile")}
                  >
                    <ListItemIcon>
                      <Person2 />
                    </ListItemIcon>
                    <ListItemText primary="Mis Datos" />
                  </ListItemButton>
                </ListItem>
              </>
            ) : (
              <>
                <ListItem disablePadding>
                  <ListItemButton onClick={handleLogin}>
                    <ListItemIcon>
                      <Person />
                    </ListItemIcon>
                    <ListItemText primary="Autenticarse" />
                  </ListItemButton>
                </ListItem>
              </>
            )}
          </List>
        </Box>
      </Drawer>
    </>
  );
}
