import { Box, Fab } from "@mui/material";
import ReactAudioPlayer from "react-audio-player";
import { VolumeOff, VolumeUp } from "@mui/icons-material";
import { useCallback, useState } from "react";

export function AudioPlayer() {
  const [player, setPlayer] = useState<ReactAudioPlayer | null>(null);
  const [isPlaying, setIsPlaying] = useState<boolean>();

  const handlePlay = useCallback(() => {
    player?.audioEl.current?.play();
  }, [player]);

  const handlePause = useCallback(() => {
    player?.audioEl.current?.pause();
  }, [player]);

  return (
    <>
      <Box position="absolute" right={10} top={60}>
        {isPlaying ? (
          <Fab color="primary" aria-label="pause" onClick={handlePause}>
            <VolumeOff />
          </Fab>
        ) : (
          <Fab color="primary" aria-label="play" onClick={handlePlay}>
            <VolumeUp />
          </Fab>
        )}
      </Box>
      <ReactAudioPlayer
        src="/assets/music/music.mp3"
        onPause={() => setIsPlaying(false)}
        onPlay={() => setIsPlaying(true)}
        ref={(p) => setPlayer(p)}
        loop />
    </>
  );
}
